<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
div(
  :class=`[
    "c-email-box",
    "c-email-box--" + direction,
    "c-email-box--" + layout,
    "c-email-box--" + size,
    "c-email-box--" + theme,
    {
      "c-email-box--reverse": reverse
    }
  ]`
)
  div(
    v-if="success ? imageLinkSuccess || imageLink : imageLink"
    :style=`{
      backgroundImage: 'url(' + (success ? imageLinkSuccess || imageLink : imageLink) + ')'
    }`
    class="c-email-box__image"
  )

  transition(
    enter-active-class="u-animated u-ms500 u-fade-in-right"
    leave-active-class="u-animated u-ms250 u-fade-out-left"
    mode="out-in"
  )
    div(
      v-if="success"
      class="c-email-box__content"
    )
      p(
        v-html="messageContentSuccess"
        class="c-email-box__message c-email-box__message--success"
      )

      base-button(
        @click="onClose"
        :full-width="true"
        :size="size"
        color="green"
      ) Close

    div(
      v-else
      class="c-email-box__content"
    )
      p(
        v-if="messageContent"
        v-html="messageContent"
        class="c-email-box__message"
      )

      field-input(
        v-model="email"
        @submit="onSubmit"
        :error="error ? errorMessage : ''"
        :placeholder="inputPlaceholder"
        :size="size"
        :theme="theme"
        :validation="$v.email"
        class="c-email-box__input"
        left-icon="email"
      )

      base-button(
        @click="onSubmit"
        :full-width="true"
        :loading="submitting"
        :size="size"
        :theme="theme"
      ) {{ buttonContent }}

      p(
        v-if="descriptionContent"
        v-html="descriptionContent"
        class="c-email-box__description"
      )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// NPM
import { validationMixin } from "vuelidate"
import { email, required } from "vuelidate/lib/validators"
import { BaseButton, FieldInput } from "@growthbunker/vuedarkmode"

// PROJECT: MIXINS
import CloseChildMixin from "@/mixins/main/CloseChildMixin"
import EmailCatcherMixin from "@/mixins/widgets/EmailCatcherMixin"
import WidgetMixin from "@/mixins/main/WidgetMixin"

export default {
  components: {
    BaseButton,
    FieldInput
  },

  mixins: [CloseChildMixin, EmailCatcherMixin, WidgetMixin, validationMixin],

  data: () => ({
    // --> STATE <--

    submitting: false,
    success: false,

    email: ""
  }),

  validations: {
    email: {
      email,
      required
    }
  },

  methods: {
    // --> EVENT LISTENERS <--

    async onSubmit() {
      this.$v.$touch()

      if (!this.$v.$invalid && !this.submitting) {
        this.submitting = true

        try {
          if (!this.editor) {
            await this.$store.dispatch("sendData", {
              data: {
                email: this.email,
                from: "email-catcher"
              }
            })
          }

          // Clear the email and reset validation
          this.email = ""
          this.$v.$touch()

          // Display a success message
          this.success = true
        } catch (error) {
          console.error(error)

          this.error = true
        }

        this.submitting = false
      }
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
