$sizes: "mini", "small", "default", "medium", "large";

#{$c} {
  display: flex;

  #{$c}__image {
    display: inline-block;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    user-select: none;
  }

  #{$c}__content {
    #{$c}__message {
      margin: 0 0 10px;

      &--success {
        text-align: center;
      }
    }

    #{$c}__input {
      box-sizing: border-box;
      margin-bottom: 10px;
      width: 100%;
    }

    #{$c}__description {
      margin: 10px 0 0;
      text-align: center;
    }
  }

  // --> DIRECTIONS <--

  &--horizontal,
  &--vertical {
    flex-direction: column;

    #{$c}__image {
      border-bottom-width: 1px;
      border-bottom-style: solid;
    }
  }

  // --> SIZES <--

  @each $size in $sizes {
    $i: index($sizes, $size) - 1;

    &--#{$size} {
      #{$c}__content {
        padding: 12px + (2px * $i);

        #{$c}__message {
          font-size: 12px + (1px * $i);
          line-height: 18px + (1px * $i);
        }

        #{$c}__description {
          font-size: 10px + (1px * $i);
          line-height: 16px + (1px * $i);
        }
      }

      &#{$c}--horizontal,
      &#{$c}--vertical {
        #{$c}__image {
          height: 100px + (20px * $i);
        }
      }
    }
  }

  // --> BOOLEANS <--

  &--reverse {
    #{$c}__image {
      order: 1;
    }

